@import url(https://fonts.googleapis.com/css2?family=Overpass:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}body{font-size:16px;background-color:#ff525d}a{text-decoration:none;color:black}li{list-style:none}.accordion{width:100%;padding-bottom:1rem}.accordion li{transition:color 0.3s ease-in-out;display:flex;align-items:center;font-size:1.2rem;font-weight:600;padding-bottom:0.5rem;font-family:"Overpass",sans-serif;color:#1f3f5b;cursor:pointer}.accordion li span{padding-left:0.5rem}.accordion li span svg{transition:transform 0.3s ease-in-out;width:15px;height:15px}.accordion div{transition:max-height 0.3s ease-in-out;max-height:0;border-radius:5px;width:100%;background-color:rgba(200,200,203,0.3);padding:0rem;overflow:hidden}.accordion div a{padding:0.5rem 0;font-weight:600;font-family:"Overpass",sans-serif;color:#4b5862}.accordion-active li{color:#4b5862}.accordion-active li span svg{transform:rotate(-180deg)}.accordion-active div{max-height:none;padding:1rem}.nav-active{transform:translateY(0) !important}header{background-color:transparent;min-height:8vh;display:flex;align-items:flex-end}header .navbar{width:80%;display:flex;align-items:center;justify-content:space-between;margin:0 auto}header .navbar .logo{font-size:2rem;font-weight:700;font-family:"Ubuntu",sans-serif;color:#fff}header .navbar .secondary-nav-links{position:absolute;transform:translateY(-110vh);top:12vh;left:10%;width:80%;background-color:#fff;display:flex;flex-direction:column;padding:1.5rem;border:none;border-radius:5px;transition:all 0.3s ease-in-out}header .navbar .secondary-nav-links div{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}header .navbar .secondary-nav-links .second-nav{border-top:2px solid rgba(200,200,203,0.4);padding-top:1rem}header .navbar .secondary-nav-links .second-nav a{padding-top:0.8rem;color:#1f3f5b;font-size:1.2rem;font-weight:600;font-family:"Overpass",sans-serif;display:flex;align-items:center}header .navbar .secondary-nav-links .second-nav .singup-btn{background-image:linear-gradient(90deg, #ff8f70, #ff3d54);padding:1rem 2.5rem;margin-top:1rem;color:#fff;border-radius:25px;font-weight:700}header .navbar .burger{cursor:pointer}header .navbar .burger div{width:25px;height:4px;background-color:#fff;margin:0.25rem 0;border:none;border-radius:10px;transition:all 0.3s ease-in-out}header .navbar .burger-active .line1{transform:rotate(45deg) translate(8px, 10px)}header .navbar .burger-active .line2{opacity:0}header .navbar .burger-active .line3{transform:rotate(-45deg) translate(2px, -3px)}

